import { Component, h } from "preact";
import HyphaTokenSale from "hypha-token-sales-util";

import Account from "../components/account";
import Welcome from "../components/welcome";
import ChooseAmount from "../components/choose-amount";
import FinalisePurchase from "../components/finalise-purchase";
import { TOKENS, DEFAULT_DISCORD_LINK } from "../config";

const STEPS = {
  ACCOUNT: "account",
  WELCOME: "welcome",
  AMOUNT: "amount",
  FINALISE: "finalise",
};

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      currentStep: STEPS.ACCOUNT,
      selectedToken: props.defaultToken || TOKENS.BTC,
      amount: 1000,
      accountName: null,
      currentRound: null,
      usdPerHypha: null,
      hyphaRemainingThisRound: null,
      loading: true,
      discordLink: props.discordLink || DEFAULT_DISCORD_LINK,
    };
  }

  async componentDidMount() {
    const { apiUrl, rpcUrl } = this.props;
    const hyphaTokenSale = new HyphaTokenSale(rpcUrl, apiUrl);
    const { usdPerHypha, hyphaRemainingThisRound, currentRound } =
      await hyphaTokenSale.init();

    const priceMap = await hyphaTokenSale.hyphaContract.getPricePerRound();
    const totalRounds = Object.keys(priceMap).length;
    const hyphaRemaining = await hyphaTokenSale.hyphaContract.getHyphaBalance();
    const btcPerHypha = await hyphaTokenSale.convertHyphaToBTC(1);
    const eosPerHypha = await hyphaTokenSale.convertHyphaToEOS(1);
    const state = Object.assign({}, this.state, {
      loading: false,
      eosPerHypha,
      btcPerHypha,
      usdPerHypha,
      priceMap,
      hyphaRemaining,
      hyphaRemainingThisRound,
      roundNo: currentRound,
      totalRounds,
    });
    this.hyphaTokenSale = hyphaTokenSale;
    this.setState(state);
  }

  setCurrentStep = (step) => {
    this.setState({ ...this.state, currentStep: step });
  };

  chooseToken = async (token) =>
    this.setState({ ...this.state, selectedToken: token });

  setAccount = (account) =>
    this.setState({ ...this.state, account, accountName: account.account });

  setHyphaAmount = (amount) => this.setState({ ...this.state, amount });

  convertHyphaToEos = (val) => {
    return val * this.state.eosPerHypha;
  };
  convertHyphaToBtc = (val) => val * this.state.btcPerHypha;
  convertHyphaToUsd = (val) => {
    const { priceMap, hyphaRemainingThisRound, roundNo } = this.state;

    const cost = this.hyphaTokenSale.hyphaContract.calculateCost({
      amount: val,
      currentRound: roundNo,
      hyphaRemainingThisRound,
      priceMap,
    });

    return Number(cost).toFixed(2);
  };

  getAccountDetails = (accountName) =>
    this.hyphaTokenSale.getAccountDetails(accountName);

  getBtcAddress = async (accountName) => {
    return await this.hyphaTokenSale.getBitcoinAddress(accountName);
  };

  render(_props, state) {
    const {
      currentStep,
      roundNo,
      selectedToken,
      usdPerHypha,
      hyphaRemaining,
      hyphaRemainingThisRound,
      accountName,
      account,
      amount,
      totalRounds,
    } = state;

    return (
      <div>
        <div>
          {currentStep === "account" && (
            <Account
              discordLink={this.state.discordLink}
              slidesLink={this.props.slidesLink || "#buyhypha-slides"}
              slidesLinkText={this.props.slidesLinkText || "Click here "}
              widgetText2={
                this.props.widgetText2 ||
                "and use your SEEDS account to proceed to the token sale"
              }
              widgetText1={
                this.props.widgetText1 || " to learn more about Hypha token"
              }
              setAccount={this.setAccount}
              getAccountDetails={this.getAccountDetails}
              next={
                state.loading ? null : () => this.setCurrentStep(STEPS.WELCOME)
              }
            />
          )}
          {currentStep === "welcome" && (
            <Welcome
              setToken={this.chooseToken}
              token={selectedToken}
              account={account}
              roundNo={roundNo}
              totalRounds={totalRounds}
              usdPerHypha={usdPerHypha}
              previous={() => this.setCurrentStep(STEPS.ACCOUNT)}
              next={() => this.setCurrentStep(STEPS.AMOUNT)}
            />
          )}
          {currentStep === "amount" && (
            <ChooseAmount
              accountName={accountName}
              token={selectedToken}
              hyphaRemaining={hyphaRemaining}
              hyphaRemainingThisRound={hyphaRemainingThisRound}
              setHyphaAmount={this.setHyphaAmount}
              amount={amount}
              convertHyphaToUsd={this.convertHyphaToUsd}
              convertHyphaToCrypto={
                selectedToken === TOKENS.EOS
                  ? this.convertHyphaToEos
                  : this.convertHyphaToBtc
              }
              previous={() => this.setCurrentStep("welcome")}
              next={() => this.setCurrentStep("finalise")}
            />
          )}
          {currentStep === "finalise" && (
            <FinalisePurchase
              amount={amount}
              getBtcAddress={this.getBtcAddress}
              convertHyphaToUsd={this.convertHyphaToUsd}
              convertHyphaToCrypto={
                selectedToken === TOKENS.EOS
                  ? this.hyphaTokenSale.convertHyphaToEOS
                  : this.hyphaTokenSale.convertHyphaToBTC
              }
              token={selectedToken}
              accountName={accountName}
              previous={() => this.setCurrentStep("amount")}
            />
          )}
        </div>
      </div>
    );
  }
}

export default App;
