import { h } from "preact";
import { useState } from "preact/hooks";
import StepActions from "../step-actions";

import { getErrorMessage } from "../../utils";

const Account = ({
  next,
  previous,
  setAccount,
  discordLink,
  slidesLink,
  slidesLinkText,
  widgetText2,
  widgetText1,
  getAccountDetails,
}) => {
  const [loading, setLoading] = useState();
  const [error, setError] = useState();
  const [accountName, setAccountName] = useState();

  const handleInputChange = (event, props) => {
    const accountName = event.target.value.toLowerCase();
    setError(false);
    setAccountName(accountName);
  };

  const getAccountAndNavigate = async () => {
    setLoading(true);
    try {
      const account = await getAccountDetails(accountName);
      await setAccount(account);
      await next();
    } catch (e) {
      setLoading(false);
      setError(e.message);
    }
  };

  return (
    <div id="step-1">
      <div class="step-nav">
        <div class="stp active">
          <p>1</p>
        </div>
        <div class="stp"></div>
        <div class="stp"></div>
        <div class="stp"></div>
      </div>

      <div class="steps-top">
        <h2>Account Name</h2>
        <p>
          <a class="simple-button-yellow no-arrow" href={slidesLink}>
            {slidesLinkText}
          </a>
          {widgetText1}
          <br />
          {widgetText2}
          <br />
          <b>
            Don't have a SEEDS account yet?
            <br />
            Visit:{" "}
            <a class="simple-button-yellow-arrow" href={discordLink}>
              Hypha Discord channel
            </a>{" "}
          </b>
        </p>
      </div>
      <div class="steps-bottom">
        <input
          id="seeds-account-name"
          placeholder="Enter SEEDS account name"
          class={error && "error"}
          onChange={handleInputChange}
        />
        {error && <div class="error-message">{getErrorMessage(error)}</div>}

        <StepActions
          next={next && getAccountAndNavigate}
          previous={previous}
          loading={loading}
        />
      </div>
    </div>
  );
};

export default Account;
