import { h, Fragment } from "preact";
import { BTC_DECIMAL_PLACES, EOS_DECIMAL_PLACES, TOKENS } from "../../config";
import EosBillCard from "./eos";
import BtcBillCard from "./btc";
import StepActions from "../step-actions";
import useAsync from "../../hooks/useAsync";
import Spinner from "../spinner";
import { formatNumber } from "../../utils";

const FinalisePurchase = ({
  convertHyphaToCrypto,
  getBtcAddress,
  convertHyphaToUsd,
  amount,
  token,
  accountName,
  next,
  previous,
}) => {
  const [cryptoAmount, error, isLoading] = useAsync(
    () =>
      convertHyphaToCrypto(amount).then(
        (calculatedCryptoAmount) => calculatedCryptoAmount
      ),
    [amount]
  );

  return (
    <div id="step-4">
      <div class="step-nav">
        <div class="stp done"></div>
        <div class="stp done"></div>
        <div class="stp done"></div>
        <div class="stp active">
          <p>4</p>
        </div>
      </div>

      <div class="steps-top">
        <p>Hypha token sale</p>
        <h2>Finalise Purchase</h2>

        <div class="bill">
          <p>
            to buy <span id="total-hypha-to-buy">{formatNumber(amount)} </span>{" "}
            <b>HYPHA tokens</b>
          </p>
          <p>
            Transfer the exact amount of
            <span id="selected-method"> {token}</span> to:
          </p>

          {token === TOKENS.EOS && (
            <>
              <EosBillCard
                accountName={accountName}
                usdAmount={isLoading ? <Spinner /> : convertHyphaToUsd(amount)}
                amount={
                  isLoading ? (
                    <Spinner />
                  ) : (
                    formatNumber(cryptoAmount, EOS_DECIMAL_PLACES)
                  )
                }
              />
              <p>Hypha will be transferred to your account in a few minutes.</p>
            </>
          )}
          {token === TOKENS.BTC && (
            <>
              <BtcBillCard
                getBtcAddress={getBtcAddress}
                accountName={accountName}
                usdAmount={isLoading ? <Spinner /> : convertHyphaToUsd(amount)}
                amount={
                  isLoading ? (
                    <Spinner />
                  ) : (
                    formatNumber(cryptoAmount, BTC_DECIMAL_PLACES)
                  )
                }
              />
              <p>
                Hypha will be transferred as soon as there is more than 1
                confirmation on your Bitcoin transaction
              </p>
            </>
          )}
        </div>
      </div>

      <div class="steps-bottom">
        <StepActions next={next} previous={previous} />
      </div>
    </div>
  );
};

export default FinalisePurchase;
