import { h } from "preact";
import { useState } from "preact/hooks";
import StepActions from "../step-actions";

import {
  getErrorMessage,
  formatNumber,
  stripNonNumericValues,
} from "../../utils";
import { BTC_DECIMAL_PLACES, EOS_DECIMAL_PLACES, TOKENS } from "../../config";

const ChooseAmount = ({
  next,
  previous,
  token,
  hyphaRemainingThisRound,
  hyphaRemaining,
  setHyphaAmount,
  amount,
  convertHyphaToCrypto,
  convertHyphaToUsd,
}) => {
  const [error, setError] = useState();
  const availableHyphaPretty = formatNumber(hyphaRemaining);
  const availableHyphaThisPretty = formatNumber(hyphaRemainingThisRound);
  const availableUsdPretty = formatNumber(convertHyphaToUsd(hyphaRemaining));

  const handleInputChange = (e) => {
    let { value } = e.target;

    const amount = stripNonNumericValues(value);
    const maxAmount = Number(hyphaRemaining);

    if (amount > maxAmount) {
      setError("high_amount");
      setHyphaAmount(maxAmount);
    } else {
      setError();
      setHyphaAmount(amount);
    }
  };
  return (
    <div id="step-3">
      <div class="step-nav">
        <div class="stp done"></div>
        <div class="stp done"></div>
        <div class="stp active">
          <p>3</p>
        </div>
        <div class="stp"></div>
      </div>

      <div class="steps-top">
        <div id="token-sale-availability">
          <p>Remaining Hypha tokens available:</p>
          <div class="cont-count">
            <p id="hypha-available">{availableHyphaPretty}</p>
            <img src="/img/round-logo.svg" />
          </div>
          <div class="remaining-this">
            <p class="p-small">Remaining tokens for this round:<br/><b> {availableHyphaThisPretty}</b></p>
          </div>
        {/* <div class="cont-count">
            <p class="to-usd">
              (<span id="hypha-to-usd">{availableUsdPretty}</span>
              <b>USD</b> )
            </p>
          </div> */}
        </div>

        <h2>Choose Amount</h2>
        <p>
          Please, type in the <b>amount of Hypha token</b> you would like to
          buy. The value of <span id="selected-method">{token}</span> and USD
          will dynamically be updated.
        </p>
      </div>
      <div class="steps-bottom">
        <div class="input-wrap">
          <input
            id="hypha-amount-to-buy"
            class={error && "warning"}
            value={amount}
            placeholder={formatNumber(amount, 2)}
            onInput={handleInputChange}
          ></input>

          {error && <div class="warning-message">{getErrorMessage(error)}</div>}
        </div>

        <div id="converter">
          <div class="conv-cript">
            <b>
              <span id="selected-method">{token}</span>
            </b>{" "}
            <span id="conversion-cripto">
              {" "}
              {formatNumber(
                convertHyphaToCrypto(amount),
                token === TOKENS.EOS ? EOS_DECIMAL_PLACES : BTC_DECIMAL_PLACES
              )}
            </span>
          </div>

          <div class="conv-usd">
            <b>USD</b>{" "}
            <span id="conversion-usd">
              {" "}
              {formatNumber(convertHyphaToUsd(amount))}
            </span>
          </div>
        </div>
        <StepActions next={next} previous={previous} />
      </div>
    </div>
  );
};

export default ChooseAmount;
